<template>
  <div>
    <h1>주식 수익률 계산기</h1>
    <p>
      화살표 버튼을 이용해 월을 변경하며 현재 대비 수익률을 확인하세요.
      <br />
      2023년 9월 기준으로 얼마나 차이가 있는지 알아보세요.
    </p>
    <div class="stock-row" v-for="stock in stocks" :key="stock.name">
      <span class="stock-name">{{ stock.name }}</span>
      <div class="buttons">
        <button @click="showPreviousMonth(stock)">▼</button>
        <span>
          {{ stock.selectedDate.split('-')[0] }}년 {{ stock.selectedDate.split('-')[1]}}월
        </span>
        <button @click="showNextMonth(stock)">▲</button>
      </div>
      <div class="stock-result" v-if="stock.result">
        <div>{{ stock.result.message }}</div>
        <div>{{ stock.result.high }}</div>
        <div class="highlight">{{ stock.result.highlight }}</div>
      </div>
      <div class="stock-result" v-else>
        <!-- 공백 표시 -->
      </div>
    </div>
  </div>
</template>

<script>
import {onMounted, reactive} from 'vue';

import apple_prices from './data/apple.js'; // 경로는 실제 위치에 따라 조정해야 할 수 있습니다.
import nvidia_prices from './data/nvidia.js';
import tesla_prices from './data/tesla.js';
import microsoft_prices from './data/microsoft.js';
import amazon_prices from './data/amazon.js';
import coca_prices from './data/coca.js';
import meta_prices from './data/meta.js';
import netfilx_prices from './data/netfilix.js';
import nike_prices from './data/nike.js';
import pepsi_prices from './data/pepsi.js';

const DUMMY_DATA = {
  apple: {
    name: "애플",
    prices: apple_prices
  },
  nvidia: {
    name: "엔비디아",
    prices: nvidia_prices
  },
  tesla: {
    name: "테슬라",
    prices: tesla_prices
  },
  ms: {
    name: "마이크로소프트",
    prices: microsoft_prices
  },
  amazon : {
    name : "아마존",
    prices : amazon_prices
  },
  cocacola : {
    name : "코카콜라",
    prices : coca_prices
  },
  meta : {
    name : "메타",
    prices : meta_prices
  },
  netflix : {
    name : "넷플릭스",
    prices : netfilx_prices
  },
  nike : {
    name : "나이키",
    prices : nike_prices
  },
  pepsi : {
    name : "펩시",
    prices : pepsi_prices
  }

};

export default {
  name: 'App',
  setup() {
    const stocks = reactive(Object.keys(DUMMY_DATA).map(stockKey => ({
      name: DUMMY_DATA[stockKey].name,
      selectedDate: "2023-08-01",
      result: "",
      prices: DUMMY_DATA[stockKey].prices,
      availableDates: Object.keys(DUMMY_DATA[stockKey].prices).sort().reverse()
    })));

    const numberWithCommas = (x) => {
      return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    };

    const differenceInMonths = (date1, date2) => {
      const [year1, month1] = date1.split('-').map(Number);
      const [year2, month2] = date2.split('-').map(Number);
      return (year2 - year1) * 12 + (month2 - month1);
    };

    const calculateProfit = (stock, date) => {
      const currentMonthPrice = stock.prices["2023-08-01"];
      const selectedMonthPrice = stock.prices[date];
      const monthsDifference = differenceInMonths(date, "2023-08-01"); // 이 부분 수정
      const profitPercent = ((currentMonthPrice - selectedMonthPrice) / selectedMonthPrice) * 100;
      const profitFrom1Million = 1000000 * profitPercent / 100;
      const formattedProfit = numberWithCommas(profitFrom1Million.toFixed(0)); // 이 부분 수정
      stock.result = {
        message: `${monthsDifference}개월 전에 ${stock.name} 샀으면`,
        high : `${profitPercent.toFixed(2)}%의 수익률. `,
        highlight: `100만원 투자 시, 약 ${formattedProfit}원 수익`
      };
    };


    const showPreviousMonth = (stock) => {
      const index = stock.availableDates.indexOf(stock.selectedDate);
      if (index < stock.availableDates.length - 1) {
        stock.selectedDate = stock.availableDates[index + 1];
        calculateProfit(stock, stock.selectedDate); // 이 부분 수정
      }
    };
    const showNextMonth = (stock) => {
      const index = stock.availableDates.indexOf(stock.selectedDate);
      if (index > 0) {
        stock.selectedDate = stock.availableDates[index - 1];
        calculateProfit(stock, stock.selectedDate); // 이 부분 수정
      }
    };

    onMounted(() => {
      stocks.forEach(stock => {
        calculateProfit(stock, stock.selectedDate);
      });
    });


    return {
      stocks,
      numberWithCommas,
      differenceInMonths,
      calculateProfit,
      showPreviousMonth,
      showNextMonth
    };
  },

}
</script>

